/*
############################################################
RESET
############################################################
*/
* {
  padding: 0px;
  margin: 0px;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box; }
  *:focus, *:focus, *:focus {
    outline: none; }

/*
############################################################
FONTS
############################################################
*/
body {
  font-family: "Open Sans", sans-serif;
  font-size: 14px; }

h1 {
  font-family: "Merriweather", serif; }
  @media screen and (max-width: 980px) {
    h1 {
      font-size: 1.75em;
      margin-bottom: 0.5em; } }
  @media screen and (min-width: 981px) and (max-width: 1400px) {
    h1 {
      font-size: 3em;
      margin-bottom: 0.5em; } }
  @media screen and (min-width: 1401px) {
    h1 {
      font-size: 4.5em;
      margin-bottom: 0.6em;
      line-height: 1.25em; } }

h2 {
  font-family: "Merriweather", serif;
  font-weight: bold;
  color: #1D3142; }
  @media screen and (max-width: 980px) {
    h2 {
      font-size: 1.25em;
      margin-bottom: 0.3em; } }
  @media screen and (min-width: 981px) and (max-width: 1400px) {
    h2 {
      font-size: 1.4em;
      margin-bottom: 0.4em; } }
  @media screen and (min-width: 1401px) {
    h2 {
      font-size: 1.7em; } }

h3 {
  font-family: "Open Sans", sans-serif;
  font-weight: normal;
  font-size: 2.3em;
  line-height: 1.1em;
  letter-spacing: -0.05em;
  color: #3E6990; }
  @media screen and (max-width: 980px) {
    h3 {
      font-size: 1.25em;
      line-height: 1.3em;
      margin-bottom: 0.4em; } }
  @media screen and (min-width: 981px) and (max-width: 1400px) {
    h3 {
      font-size: 1.8em;
      line-height: 1.3em;
      margin-bottom: 0.4em; } }

/* Add spacing to sections */
section {
  margin: 50px 0px; }

/*
############################################################
BUTTONS
############################################################
*/
.btn-md, .btn-sm {
  text-transform: uppercase;
  display: inline-block;
  cursor: pointer;
  text-decoration: none; }

.btn-md {
  border-radius: 28px;
  padding: 11px 46px 12px;
  font-size: 0.9em;
  font-weight: 500; }
  @media screen and (min-width: 1401px) {
    .btn-md {
      padding: 13px 51px 14px;
      font-size: 1em; } }

.btn-sm {
  border-radius: 28px;
  padding: 9px 35px;
  font-size: 0.75em; }
  @media screen and (min-width: 1401px) {
    .btn-sm {
      font-size: 0.9em; } }

.btn-orange {
  color: white;
  background: linear-gradient(to bottom, #f39a6b 5%, #ef783c 100%);
  background-color: #f39a6b;
  text-shadow: 0px 1px 0px #F0F0F0; }

.btn-green {
  color: white;
  background: linear-gradient(to bottom, #88a25e 5%, #6b883b 100%);
  background-color: #88a25e;
  text-shadow: 0px 1px 0px #F0F0F0; }

.btn-transparent {
  color: white;
  background-color: transparent;
  transition: background-color 200ms ease-out;
  border: 1px solid #3E6990;
  color: #3E6990; }
  .btn-transparent:hover {
    background-color: #3E6990;
    color: white; }

/*
