/*
############################################################
COMMON CLASSES
############################################################
*/
.justify-center {
  margin: 0 auto;
  min-width: 300px; }

.justify-stretch {
  display: block;
  width: 100%; }

.clearfix:after {
  content: "";
  display: table;
  clear: both; }

/*

### Breakpoint: Mobile ### 

*/
@media screen and (max-width: 980px) {
  .sm-hide {
    display: none; }
  .justify-center,
  .sm-justify-center {
    width: 62vw; }
  .sm-justify-center {
    margin: 0 auto; }
  .sm-justify-stretch {
    display: block;
    width: 100%; } }

/*

### Breakpoint: Tablet ### 

*/
@media screen and (min-width: 981px) and (max-width: 1400px) {
  .md-hide {
    display: none; }
  .md-justify-stretch {
    width: auto; }
